const onWaypoint = function({ el: element, going }) {
    // going: in, out
    // direction: top, right, bottom, left
    const animation = ["animated", ...element.dataset.animate.split(" ")].map((i) => "animate__" + i);
    switch (going) {
        case "in":
            element.classList.add(...animation);
            break;
        case "out":
            element.classList.remove(...animation);
            break;
    }
    element.addEventListener(
        "animationend",
        function(e) {
            this.classList.remove(...animation);
            if (this.dataset.animateCallback) {
                this.dataset.animateCallback(e, this);
            }
        }.bind(element)
    );
};

export { onWaypoint };
